import React, {useEffect, useState} from 'react';
import {useNavigate} from "react-router-dom";
import {registerAsClient} from "../../../services/auth.service";
import ConfirmPolitics from "../../../components/ConfirmPolitics/ConfirmPolitics";
import {ConfirmPoliticsContext} from "../../../components/ConfirmPolitics/ConfirmPoliticsContext";
// import Error from "../../../components/Error/Error";
import styles from './RegistrationUserPage.module.scss';

const RegistrationUserPage = () => {
  useEffect(() => {
    document.title = '';
  }, []);

  const navigate = useNavigate();

  const [error, setError] = useState();
  const [name, setName] = useState("");
  const [lastname, setLastname] = useState("");
  const [email, setEmail] = useState("");
  const [phone, setPhone] = useState("");
  const [password, setPassword] = useState("");
  const [passwordVerification, setPasswordVerification] = useState("");
  const [accept, setAccept] = useState(false);

  const onSubmit = (e) => {
    e.preventDefault()

    if (!accept) {
      // @ts-ignore
      return setError("     .");
    }

    return registerAsClient({
      name,
      lastname,
      email,
      phone,
      password1: password,
      password2: passwordVerification,
    })
      .then(() => navigate("/login"))
      .catch((err) => setError(err.message))
  };

  return (
    <ConfirmPoliticsContext.Provider value={{accept, setAccept}}>
      <div className={`${styles.registrationUserPage} appContainer`}>
        <h1 className={styles.registrationUserPage_title}></h1>
         <form className={styles.registrationUserPage_form} onSubmit={onSubmit}>
           {/*{error && (*/}
           {/*//    className="auth-err"*/}
           {/*<Error error={error} />*/}
           {/*)}*/}
           <input
             className={styles.registrationUserPage_form_input}
             type="text"
             name="name"
             placeholder=""
             value={name}
             onChange={(e) => setName(e.target.value)}
             required
           />
           <input
             className={styles.registrationUserPage_form_input}
             type="text"
             name="lastname"
             placeholder=""
             value={lastname}
             onChange={(e) => setLastname(e.target.value)}
             required
           />
           <input
             className={styles.registrationUserPage_form_input}
             type="email"
             name="email"
             placeholder="Email"
             value={email}
             onChange={(e) => setEmail(e.target.value)}
             required
           />
           <input
             className={styles.registrationUserPage_form_input}
             type="text"
             name="phone"
             placeholder=""
             value={phone}
             onChange={(e) => setPhone(e.target.value)}
             required
           />
           <input
             className={styles.registrationUserPage_form_input}
             type="password"
             name="password"
             placeholder=""
             value={password}
             onChange={(e) => setPassword(e.target.value)}
             required
           />
           <input
             className={styles.registrationUserPage_form_input}
             type="password"
             placeholder=" "
             value={passwordVerification}
             onChange={(e) => setPasswordVerification(e.target.value)}
             required
           />

           {/*   , ..  */}
           <ConfirmPolitics />

           <button className={styles.registrationUserPage_form_button} type="submit"></button>
         </form>
      </div>
    </ConfirmPoliticsContext.Provider>
  );
};

export default RegistrationUserPage;